<script>
export default {
  name: "ProductDisplay",
  data() {
    return {
      amount: 0,
      price: 75,
      finalPrice: 0,
      isAmountGreaterThan3: false,
      isAmountLessThan0: false,
      isAmountAllowed: false,
    };
  },
  methods: {
    addToCart() {
      if (this.amount > 3) {
        this.isAmountGreaterThan3 = true;
        this.isAmountLessThan0 = false;
        this.isAmountAllowed = false;
      } else if (this.amount > 0 && this.amount < 4) {
        this.isAmountAllowed = true;
        this.isAmountGreaterThan3 = false;
        this.isAmountLessThan0 = false;
        this.finalPrice = this.amount * this.price;
      } else if (this.amount < 1) {
        this.isAmountLessThan0 = true;
        this.isAmountGreaterThan3 = false;
        this.isAmountAllowed = false;
      }
    },
  },
};
</script>

<template>
  <div class="flex w-full h-screen">
    <!-- Sisi Kiri -->
    <div class="bg-gray-200 w-1/2 flex">
      <!-- Gambar Produk -->
      <img class="m-auto" src="../assets/img/sofa.png" alt="" />
    </div>

    <!-- Sisi Kanan -->
    <div class="w-1/2 h-screen">
      <!-- Top Detail -->
      <div class="p-16 mx-auto">
        <h1 class="font-bold text-5xl mb-5">Greyana Sofa</h1>
        <p>
          Introducing the Greyana Sofa. The perfect combination of style and
          comfort. With its sleek and modern design, this sofa is a statement
          piece that will elevate any living space. The Greyana Sofa features a
          stunning grey upholstery that is both soft to the touch and durable
          for everyday use. Crafted with a sturdy wooden frame and high-density
          foam cushions, the Greyana Sofa offers unbeatable comfort and support.
          The seat and back cushions are also removable and reversible, making
          it easy to maintain and keep looking like new.
        </p>
      </div>

      <!-- Bottom Detail -->
      <div class="p-16 mx-auto">
        <h3 class="font-semibold text-3xl">$75</h3>
        <div class="flex">
          <button @click="amount--" class="w-12 h-8 bg-black text-white">
            -
          </button>
          <div class="w-12 h-8 bg-gray-100 text-center text-lg">
            {{ amount }}
          </div>
          <button @click="amount++" class="w-12 h-8 bg-black text-white">
            +
          </button>
        </div>

        <!-- error msg -->
        <div v-if="isAmountGreaterThan3" class="mb-20 text-red-500">
          maximum items that can be added is 3
        </div>
        <!-- <div :class="isAmountGreaterThan3 ? 'block' : 'hidden'" class="mb-20 text-red-500">maximum items that can be added is 3</div> -->

        <!-- Succes msg -->
        <div v-else-if="isAmountAllowed" class="mb-20 text-green-400">
          <p>successfully added to cart</p>
        </div>

        <!-- error msg if amount 0 -->
        <div v-else-if="isAmountLessThan0" class="mb-20 text-red-500">
          Minimum item that can be added is 1
        </div>

        <p class="mb-6 text-2xl">Sub Total: ${{ finalPrice }}</p>
        <div class="flex gap-4">
          <button class="w-1/2 h-14 bg-black text-white">Buy Now</button>
          <button @click="addToCart" class="w-1/2 bg-black text-white">
            Add to Cart
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
